@import "../variables";
.datePick {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  &__wrapper {
    width: 360px;
    margin-left: 20px;
    position: relative;
    padding: 0 15px;
    height: 45px;
  }
  &__control {
    width: 100%;
    height: 100%;
    display: block;
    & input {
      width: 100%;
      height: 100%;
      display: block;
      font-family: $main-ff;
      color: $text-color-main;
      font-size: 14px;
      border: none;
    }
    & .react-datepicker__input-container {
      width: 100%;
      height: 100%;
      display: block;
    }
    & .react-datepicker-wrapper {
      width: 100%;
      height: 100%;

      display: block;
    }
  }
}
